import React, { useEffect, useState } from "react";
import "./SortedBinaryTree.css";
import { motion, AnimatePresence } from "framer-motion";

class TreeNode {
  constructor(value) {
    this.value = value;
    this.left = null;
    this.right = null;
  }
}

const BinaryTree = () => {
  const [root, setRoot] = useState(null);
  const [searchedNode, setSearchedNode] = useState(null);
  const [searchedNodeInput, setSearchedNodeInput] = useState("");
  const [showRandomMinMaxInput, setShowRandomMinMaxInput] = useState(false);
  const [showInputFields, setShowInputFields] = useState(false);
  const [deletedNodeInput, setDeletedNodeInput] = useState("");
  const [inputValue, setInputValue] = useState("");
  const [range, setRange] = useState({ min: "", max: "" });
  const [scale, setScale] = useState(1);
  const [addRandomErrorMessage, setAddRandomErrorMessage] = useState("");
  const [inorderTraversalPath, setInorderTraversalPath] = useState([]);
  const [postOrderTraversalPath, setPostOrderTraversalPath] = useState([]);

  const calculateScale = () => {
    const pageContainer = document.getElementById("page-container");
    const treenode = document.getElementById("treenode");
    const treeContainer = document.getElementById("tree-container");
    const scaleY = (pageContainer.offsetWidth / treenode.offsetWidth) * 0.9;
    setScale(scaleY);

    if (treeContainer) {
      treeContainer.scrollLeft = treeContainer.offsetWidth;
    }
  };

  useEffect(() => {
    window.addEventListener("resize", calculateScale);
    calculateScale();

    return () => {
      window.removeEventListener("resize", calculateScale);
    };
  }, [root]);

  const insertNode = (node, value) => {
    if (!node) {
      return new TreeNode(value);
    }

    if (value < node.value) {
      node.left = insertNode(node.left, value);
    } else if (value > node.value) {
      node.right = insertNode(node.right, value);
    }
    var newNode = new TreeNode(node.value);
    newNode.left = node.left;
    newNode.right = node.right;
    return newNode;
  };

  const handleAdd = (value) => {
    if (value !== null) {
      setRoot((prevRoot) => insertNode(prevRoot, Number(value)));
      setInputValue("");
    }
  };

  const handleSearch = (node, searchValue) => {
    if (!node) {
      console.log("not found!");
      return;
    }
    if (searchValue && node.value === searchValue) {
      setSearchedNode(node);
      return;
    }

    if (searchValue < node.value) {
      handleSearch(node.left, searchValue);
    }

    if (searchValue > node.value) {
      handleSearch(node.right, searchValue);
    }
  };

  const handleDelete = () => {
    setRoot((r) => deleteNode(r, deletedNodeInput));
    setDeletedNodeInput("");
  };

  const deleteNode = (node, value) => {
    if (!node) {
      return;
    }

    if (node.value > value) {
      node.left = deleteNode(node.left, value);
    }

    if (node.value < value) {
      node.right = deleteNode(node.right, value);
    }
    if (node.value === value) {
      if (!node.left) {
        return node.right;
      }

      if (!node.right) {
        return node.left;
      }

      var minimumnode = getMinimumNode(node.right);
      node.value = minimumnode.value;
      node.right = deleteNode(node.right, minimumnode.value);
    }
    return node;
  };

  const getMinimumNode = (node) => {
    if (!node.left) {
      return node;
    }

    return getMinimumNode(node.left);
  };

  const handleMinMaxChange = (key, value) => {
    setRange((prev) => ({ ...prev, [key]: value }));
  };

  const handleAddRandomNode = () => {
    if (range.max <= range.min) {
      setAddRandomErrorMessage("Invalid random node range");
      return;
    }
    setAddRandomErrorMessage("");
    var randomNum =
      Math.floor(Math.random() * (Number(range.max) - Number(range.min) + 1)) +
      Number(range.min);
    handleAdd(randomNum);
  };

  const toggleShowRandomMinMaxInput = () => {
    setShowRandomMinMaxInput((p) => !p);
  };

  const toggleShowInputFields = () => {
    setShowInputFields((p) => !p);
    setShowRandomMinMaxInput(false);
  };

  const zoomIn = () => {
    setScale((s) => 1.3 * s);
  };

  const zoomOut = () => {
    setScale((s) => 0.8 * s);
  };

  const getInorderTraversal = () => {
    if (!root) {
      return;
    }
    setInorderTraversalPath([]);
    doInorderTraversal(root);
  };

  const doInorderTraversal = async (node) => {
    if (!node) {
      return;
    }

    await doInorderTraversal(node.left);

    await new Promise((resolve) => setTimeout(resolve, 500));
    setInorderTraversalPath((p) => [...p, node.value]);
    setSearchedNode(node);

    await doInorderTraversal(node.right);
  };

  const getPostorderTraversal = () => {
    if (!root) {
      return;
    }
    setPostOrderTraversalPath([]);
    doPostorderTraversal(root);
  };

  const doPostorderTraversal = async (node) => {
    if (!node) {
      return;
    }

    await doPostorderTraversal(node.left);
    await doPostorderTraversal(node.right);

    await new Promise((resolve) => setTimeout(resolve, 500));
    setPostOrderTraversalPath((p) => [...p, node.value]);
    setSearchedNode(node);
  };

  return (
    <div className="w-screen">
      <div
        id="page-container"
        style={{
          maxWidth: "100%",
          padding: "30px",
          display: "flex",
          flexDirection: "row",
          justifyContent: "flex-start",
          // border: "1px solid #ccc",
        }}
      >
        <div style={{ position: "absolute", zIndex: 9998 }}>
          <button
            onClick={toggleShowInputFields}
            style={{ width: "30px", height: "35px" }}
          >
            <svg fill="#FFFFFF" viewBox="0 0 330 330">
              <g id="SVGRepo_iconCarrier" strokeWidth="3">
                <path
                  id="XMLID_225_"
                  d="M325.607,79.393c-5.857-5.857-15.355-5.858-21.213,0.001l-139.39,139.393L25.607,79.393 c-5.857-5.857-15.355-5.858-21.213,0.001c-5.858,5.858-5.858,15.355,0,21.213l150.004,150c2.813,2.813,6.628,4.393,10.606,4.393 s7.794-1.581,10.606-4.394l149.996-150C331.465,94.749,331.465,85.251,325.607,79.393z"
                ></path>
              </g>
            </svg>
          </button>
          <AnimatePresence>
            {showInputFields && (
              <motion.div
                initial={{
                  opacity: 0,
                  x: -70,
                  y: -70,
                  scale: 0.5,
                }}
                animate={{
                  y: 0,
                  x: 0,
                  opacity: 100,
                  scale: 1,
                }}
                exit={{
                  opacity: 0,
                  x: -70,
                  y: -70,
                  scale: 0.5,
                }}
                className="input-fields"
                style={{
                  border: "1px solid #ccc",
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "space-between",
                  alignContent: "center",
                  width: "400px",
                  minHeight: "300px",
                  background: "white",
                }}
              >
                <div className="add-node">
                  <input
                    type="number"
                    value={inputValue}
                    onChange={(e) => setInputValue(e.target.value)}
                    onKeyDown={(e) => {
                      if (e.key === "Enter") handleAdd(inputValue);
                    }}
                    placeholder="Node value"
                  />
                  <button onClick={() => handleAdd(inputValue)}>
                    Add to Tree
                  </button>
                </div>
                <div className="search-node">
                  <input
                    type="number"
                    value={searchedNodeInput}
                    onChange={(e) => setSearchedNodeInput(e.target.value)}
                    onKeyDown={(e) => {
                      if (e.key === "Enter")
                        handleSearch(root, searchedNodeInput);
                    }}
                    placeholder="Search for a node"
                  />
                  <button onClick={() => handleSearch(root, searchedNodeInput)}>
                    Search
                  </button>
                </div>
                <div className="delete-node">
                  <input
                    type="number"
                    value={deletedNodeInput}
                    onChange={(e) => setDeletedNodeInput(e.target.value)}
                    onKeyDown={(e) => {
                      if (e.key === "Enter") {
                        handleDelete();
                      }
                    }}
                    placeholder="Delete a node"
                  />
                  <button onClick={handleDelete}>Delete</button>
                </div>
                <div
                  className="random-node"
                  style={{
                    position: "relative",
                    width: "500px",
                    margin: "12px",
                  }}
                >
                  <button
                    style={{ display: "flex", alignItems: "center" }}
                    onClick={toggleShowRandomMinMaxInput}
                  >
                    <span>Add random node</span>
                    <div
                      style={{ padding: "5px", width: "25px", height: "25px" }}
                    >
                      <svg fill="#FFFFFF" viewBox="0 0 330 330">
                        <g id="SVGRepo_iconCarrier" strokeWidth="3">
                          <path
                            id="XMLID_225_"
                            d="M325.607,79.393c-5.857-5.857-15.355-5.858-21.213,0.001l-139.39,139.393L25.607,79.393 c-5.857-5.857-15.355-5.858-21.213,0.001c-5.858,5.858-5.858,15.355,0,21.213l150.004,150c2.813,2.813,6.628,4.393,10.606,4.393 s7.794-1.581,10.606-4.394l149.996-150C331.465,94.749,331.465,85.251,325.607,79.393z"
                          ></path>
                        </g>
                      </svg>
                    </div>
                  </button>
                  <AnimatePresence>
                    {showRandomMinMaxInput && (
                      <motion.div
                        className="min-max-input"
                        initial={{
                          opacity: 0,
                          x: -60,
                          y: -20,
                          scale: 0.5,
                        }}
                        animate={{
                          y: 0,
                          x: 0,
                          opacity: 100,
                          scale: 1,
                        }}
                        exit={{
                          opacity: 0,
                          x: -60,
                          y: -20,
                          scale: 0.5,
                        }}
                        style={{
                          zIndex: 9999,
                          flexDirection: "column",
                          position: "absolute",
                          left: "0",
                          padding: "10px",
                          background: "#fff",
                          border: "1px solid #ccc",
                          boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
                        }}
                      >
                        <label>
                          Min Value:
                          <input
                            type="number"
                            className="w-20"
                            value={range.min}
                            onChange={(e) =>
                              handleMinMaxChange("min", e.target.value)
                            }
                          />
                        </label>
                        <label>
                          Max Value:
                          <input
                            type="number"
                            className="w-20"
                            value={range.max}
                            onChange={(e) =>
                              handleMinMaxChange("max", e.target.value)
                            }
                          />
                        </label>
                        <div style={{ height: "30px", position: "relative" }}>
                          <button
                            onClick={handleAddRandomNode}
                            style={{
                              height: "30px",
                              alignSelf: "center",
                            }}
                          >
                            Add
                          </button>
                        </div>
                        {addRandomErrorMessage && (
                          <div
                            class="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative my-2"
                            role="alert"
                          >
                            <span class="block sm:inline">
                              {addRandomErrorMessage}
                            </span>
                            <span class="absolute top-0 bottom-0 right-0 px-4 py-3">
                              <svg
                                class="fill-current h-6 w-6 text-red-500"
                                role="button"
                                xmlns="http://www.w3.org/2000/svg"
                                viewBox="0 0 20 20"
                              ></svg>
                            </span>
                          </div>
                        )}
                      </motion.div>
                    )}{" "}
                  </AnimatePresence>
                </div>

                <div
                  className="in-order-traversal"
                  style={{
                    margin: "12px",
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <button onClick={() => getInorderTraversal(root)}>
                    In-order Traversal
                  </button>
                  {inorderTraversalPath.length > 0 && (
                    <div
                      style={{
                        marginTop: "10px",
                        padding: "3px",
                        backgroundColor: "#f9f9f9",
                        border: "1px solid #ccc",
                        borderRadius: "5px",
                        color: "#333",
                        fontSize: "18px",
                        display: "inline-block",
                      }}
                    >
                      {inorderTraversalPath.join(", ")}
                    </div>
                  )}
                </div>

                <div
                  className="post-order-traversal"
                  style={{
                    margin: "12px",
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <button onClick={() => getPostorderTraversal(root)}>
                    Post-order Traversal
                  </button>
                  {postOrderTraversalPath.length > 0 && (
                    <div
                      style={{
                        marginTop: "10px",
                        padding: "3px",
                        backgroundColor: "#f9f9f9",
                        border: "1px solid #ccc",
                        borderRadius: "5px",
                        color: "#333",
                        fontSize: "18px",
                        display: "inline-block",
                      }}
                    >
                      {postOrderTraversalPath.join(", ")}
                    </div>
                  )}
                </div>
              </motion.div>
            )}
          </AnimatePresence>
        </div>

        <div
          className="zoom-inputs"
          style={{
            position: "absolute",
            right: "50px",
            zIndex: "9999",
          }}
        >
          {" "}
          <div style={{ padding: "3px" }}>
            <button onClick={zoomIn}>
              <svg
                fill="#FFFFFF"
                height="35px"
                width="35px"
                viewBox="0 0 512 512"
              >
                <path
                  d="M325.8,0C223,0,139.6,83.4,139.6,186.2c0,33.5,9,64.8,24.4,92L0,442.2l23.3,46.5L69.8,512l164-164
	c27.2,15.5,58.5,24.4,92,24.4C428.6,372.4,512,289,512,186.2S428.6,0,325.8,0z M325.8,314.2c-70.7,0-128-57.3-128-128
	c0-70.7,57.3-128,128-128c70.7,0,128,57.3,128,128C453.8,256.9,396.5,314.2,325.8,314.2z M349.1,116.4h-46.5v46.5H256v46.5h46.5V256
	h46.5v-46.5h46.5v-46.5h-46.5V116.4z"
                />
              </svg>
            </button>
          </div>
          <div style={{ padding: "3px" }}>
            <button onClick={zoomOut}>
              <svg
                fill="#FFFFFF"
                height="35px"
                width="35px"
                viewBox="0 0 512 512"
              >
                <path
                  d="M325.8,0C223,0,139.6,83.4,139.6,186.2c0,33.5,9,64.8,24.4,92L0,442.2l23.3,46.5L69.8,512l164-164
	c27.1,15.5,58.5,24.4,92,24.4C428.6,372.4,512,289,512,186.2S428.6,0,325.8,0z M325.8,314.2c-70.7,0-128-57.3-128-128
	c0-70.7,57.3-128,128-128c70.7,0,128,57.3,128,128C453.8,256.9,396.5,314.2,325.8,314.2z M256,209.5h139.6v-46.5H256V209.5z"
                />
              </svg>
            </button>
          </div>
        </div>
        <div
          id="tree-container"
          style={{
            width: "100%",
            // border: "1px solid #ccc",
            overflowX: "auto",
            height: "max",
          }}
        >
          <div
            id="treenode"
            style={{
              minWidth: "max-content",
              transform: `scale(${scale})`,
              transformOrigin: "top",
              transition: "transform 0.2s ease-out",
            }}
          >
            <TreeDisplay node={root} searchedNode={searchedNode} />
          </div>
        </div>
      </div>
    </div>
  );
};

const TreeDisplay = ({ node, searchedNode }) => {
  if (!node) {
    return (
      <div
        className="tree-container"
        style={{
          marginLeft: "20px",
          padding: "10px",
        }}
      ></div>
    );
  }

  return (
    <div
      className="tree-container"
      style={{
        marginLeft: "5px",
        marginRight: "5px",
        // border: "1px solid #ccc",
        // padding: "10px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <div
        className="node-value"
        style={{
          backgroundColor: node === searchedNode ? "red" : "",
        }}
      >
        {node.value}
      </div>
      <motion.svg
        width="100%"
        height="100"
        style={{
          // border: "1px solid #ccc",
          display: "flex",
          justifyContent: "center",
        }}
      >
        {node.left && (
          <motion.line
            x1="50%"
            y1="0"
            x2="25%"
            y2="100"
            stroke="#00cc88"
            strokeWidth="2"
          />
        )}
        {node.right && (
          <motion.line
            x1="50%"
            y1="0"
            x2="75%"
            y2="100%"
            stroke="#00cc88"
            strokeWidth="2"
          />
        )}
      </motion.svg>

      <div
        className="children-container"
        style={{
          display: "flex",
          minWidth: "90%",
          flexDirection: " row",
          // border: "1px solid blue",
          alignItems: "",
        }}
      >
        <div
          // ref={leftChildRef}
          style={{ justifySelf: "flex-start", width: "50%" }}
        >
          <div className="childnode-container">
            <TreeDisplay node={node.left} searchedNode={searchedNode} />
          </div>
        </div>
        <div
          // ref={rightChildRef}
          style={{ justifySelf: "flex-end", width: "50%" }}
        >
          <div className="childnode-container">
            <TreeDisplay node={node.right} searchedNode={searchedNode} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default BinaryTree;
